@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
* {
  scroll-behavior: smooth;
}
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "PT Sans", sans-serif;
}

/* Navigation Start */
.links {
  margin: 0 10px;
}
@media (max-width: 768px) {
  .links {
    margin: 10px 0;
  }
}
/* Navigation End */

/* Home Page Start */
.homepage {
  width: 100%;
  height: 100vh;
  position: relative;
}
.homepage .welcomepage {
  width: 100%;
  height: 100vh;
}
.homepage .welcomepage img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}
.topmobiles {
  width: 100%;
  height: auto;
}
.topmobiles .mobiles,
.mobiles {
  width: 100%;
  height: auto !important;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 20px 0;
}
.topmobiles .mobiles .card,
.mobiles .card {
  width: 160px;
  height: 425px;
  margin: 20px;
  border: none;
  border-radius: 10px;
  transition: 0.3s;
}
.card img {
  margin: 2px auto;
  border: none;
  width: 150px;
}
.card h5,
.card p {
  margin: 0px;
  padding: 0px;
}
.card:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
}
/* Home Page End */

/* Product Detail Page Start */
.productdetail .row .col-4 {
  width: 100%;
}
.productdetail .row .col-4 .carousel {
  height: 100vh;
}
.productdetail .row .col-4 .carousel img {
  width: 100%;
  max-height: 600px;
  margin: auto 0;
}
.carousel {
  min-height: 100vh !important;
  height: 100vh !important;
  max-height: 100vh !important;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  vertical-align: center;
}
.carousel img {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-self: center;
          align-self: center;
}
.filtername {
  color: #fff;
  margin: 10px;
  border-radius: 10px;
  transition: 0.3s;
}
.filtername:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
/* Product Detail Page End */

/* Cart Item Start */
.cart {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.cartitem {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 400px;
  border: 1px solid #eee;
  margin: 20px;
}
.cartitem img {
  width: 120px;
  margin: 10px;
}
.cartitem .content {
  display: -webkit-flex;
  display: flex;
  margin: 10px;
  -webkit-flex-direction: column;
          flex-direction: column;
}
/* Cart Item End */

